const Mock = require('mockjs');
//  mock.Random 
const Random = Mock.Random;
// mock
const produceNewsData = function () {
    let articles = [];
    for (let i = 0; i < 100; i++) {
        let newArticleObject = {
            title: Random.csentence(5, 30), //  Random.csentence( min, max )
            thumbnail_pic_s: Random.dataImage('300x250', 'mock'), // Random.dataImage( size, text )  Base64 
            author_name: Random.cname(), // Random.cname() 
            date: Random.date() + ' ' + Random.time() // Random.date(),yyyy-MM-ddRandom.time() 
        }
        articles.push(newArticleObject)
    }

    return {
        articles: articles
    }
}

const postsLists = function () {
    let postsLists = {
        "code": "000",
        "post_preview_lists": []
    }
    for (let i = 0; i < 8; i++) {
        let newArticleObject = {
            post_info: Random.csentence(5, 30), //  Random.csentence( min, max )
            // thumbnail_pic_s: Random.dataImage('300x250', 'mock'), // Random.dataImage( size, text )  Base64 
            tags: [
                {
                    "tag_name": Random.cname()
                }], // Random.cname() 
            post_time: Random.date() // Random.date(),yyyy-MM-ddRandom.time() 
        }
        postsLists.post_preview_lists.push(newArticleObject)
    }
    return postsLists
}

const postTags = function () {
    let postTags = {
        "code": "000",
        "tags": [],
        "posts":{
            "tag":"centos",
            "post_preview_lists":[]
        }
    }
    for (let i = 0; i < 8; i++) {
        let newArticleObject = {
            tag_name: Random.cname(3, 10), //  Random.csentence( min, max )
        }
        postTags.tags.push(newArticleObject)
    }
    for (let i = 0; i < 10; i++) {
        let newArticleObject = {
            post_info: Random.csentence(5, 30), //  Random.csentence( min, max )
        }
        postTags.posts.post_preview_lists.push(newArticleObject)
    }
    return postTags
}

// Mock.mock( url, post/get , )
Mock.mock('/news/index', 'post', produceNewsData);
Mock.mock('/post/lists', 'get', postsLists);
Mock.mock('/tags/lists', 'get', postTags);


